<template>
    <v-container style="width: 100%; " align="center">
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
        >
            <v-row>
                <!--<v-col></v-col>-->
                <v-col class="text-center">
                    <v-toolbar-title>{{eventsText}}</v-toolbar-title>
                    <!--<v-btn  color="primary" @click="getMessagingToken">
                        <p>uwu2</p>
                    </v-btn>-->
                </v-col>
                <!-- <v-col class="text-right" style="padding-top: 0; padding-bottom: 0"><v-btn class="app-bar-button" small style="display: inline;" text color="primary"
                                                                                            @click="openProfileEdit">{{editText}}
                 </v-btn></v-col>-->
            </v-row>


        </v-app-bar>
        <v-row v-if="events.length > 0" dense style="margin-top: 60px">
            <div
                    style="width: 100%"
                    v-for="event in events"
                    :key="event.id"
            >
                <v-card
                        class="mx-auto"
                        max-width="400"
                        min-width="280"
                        @click="openEventDescription(event.id)"
                >
                    <v-img
                            class="white--text align-end"
                            height="150px"
                            :src="event.headerImg"
                            style="position: relative"
                    >

                        <v-chip
                                v-if="event.distance != null"
                                style="display:inline; float:right; margin-right: 5px; bottom: 5px"
                                color="primary"
                                small
                        >
                            <p class="text-with-icon ma-1"><v-icon style="display: inline; vertical-align: top; font-size:18px;">mdi-map-marker-outline</v-icon> {{event.distance.toFixed(2)}} km</p>
                        </v-chip>
                    </v-img>

                    <v-card-text class="text--primary" style="padding-bottom: 2px!important;">

                        <div style="display: flex; position: relative;">
                            <div style="margin-top: 10.5px">
                                <strong style="font-size: 46px;">{{getDay(event.date)}}</strong>
                            </div>
                            <div style="margin-left: 5px">
                                <div>
                                    <strong>{{getMonth(event.date)}}</strong>
                                </div>
                                <div>
                                    <strong>{{getYear(event.date)}}</strong>
                                </div>
                            </div>
                            <div style="margin-left: 25px">
                                <div>
                                    <strong style="font-size: 18px; margin-bottom: 0px">"{{event.name}}"</strong>
                                </div>
                                <div>
                                    <p class="text-with-icon"><v-icon style="display: inline;  vertical-align: top; font-size:18px;">mdi-store</v-icon> {{event.location.place}} ({{event.location.city}})</p>
                                </div>
                            </div>
                        </div>
                        <v-spacer></v-spacer>
                    </v-card-text>
                </v-card>
                <br>
            </div>
        </v-row>
        <v-row v-if="events.length == 0">
            <p>{{problemRetrievingEventsText}}</p>
        </v-row>
        <v-row>
            <v-col style="padding-top: 0px!important">
                <v-container class="pa-0">
                    <v-btn color="primary" @click="listEvents(false)" v-if="!buttonIsDisabled"
                           :disabled="buttonIsDisabled"
                           class="bottom-centered-buttons">
                        {{watchMoreButtonText}}
                    </v-btn>
                </v-container>
            </v-col>
        </v-row>
        <!--<div>
            <h2>Search and add a pin</h2>
            <label>
                <gmap-autocomplete
                        @place_changed="setPlace">
                </gmap-autocomplete>
                <button @click="addMarker">Add</button>
            </label>
            <br/>

        </div>
        <br>
        <gmap-map
                :center="center"
                :zoom="12"
                style="width:100%;  height: 400px;"
        >
            <gmap-marker
                    :key="index"
                    v-for="(m, index) in markers"
                    :position="m.position"
                    @click="center=m.position"
            ></gmap-marker>
        </gmap-map>-->
    </v-container>
</template>

<script>
    import {mapActions, mapState, mapMutations, mapGetters} from 'vuex';
    import Constants from "../../store/Constants";

    import axios from 'axios';
    import * as firebase from "firebase";

    export default {
        name: "EventsHome",
        data() {
            return {
                // default to Montreal to keep it simple
                // change this to whatever makes sense
                problemRetrievingEventsText: Constants.TEXT_PROBLEM_GETTING_EVENTS,
                watchMoreButtonText: Constants.TEXT_WATCH_MORE,
                buttonIsDisabled: true,
                center: {lat: 45.508, lng: -73.587},
                markers: [],
                places: [],
                currentPlace: null,
                eventsText: Constants.TEXT_EVENTS
            };
        },

        /*mounted() {
            //this.geolocate();
            this.center = this.$store.getters.getUserInfoLocation;
            console.log(this.center);
        },*/

        methods: {
            /*...mapActions([
                'listEventsEventsPageFirebase',
                'isNextEventsEventsPageEmptyFirebase',
            ]),*/

            /*...mapMutations([
                'setActiveEventID'
            ]),*/
            ...mapActions('events', ['getEvents', 'resetState']),
            getMonth(date) {
                return this.$store.getters.getFormattedMonthIn3CapitalLettersText(date.toDate());
            },
            getDay(date) {
                return date.toDate().getDate();
            },
            getYear(date) {
                return date.toDate().getFullYear();
            },
            listEvents: function () {
                let self = this;
                self.buttonIsDisabled = true;
                let resultGetEvents = self.getEvents();

                resultGetEvents.then((moreEventsToShow) => {
                    self.buttonIsDisabled = !moreEventsToShow;
                })
                    .catch((error) => {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },
            // receives a place object via the autocomplete component
            setPlace(place) {
                this.currentPlace = place;
            },
            addMarker() {
                if (this.currentPlace) {
                    const marker = {
                        lat: this.currentPlace.geometry.location.lat(),
                        lng: this.currentPlace.geometry.location.lng()
                    };
                    this.markers.push({position: marker});
                    this.places.push(this.currentPlace);
                    this.center = marker;
                    this.currentPlace = null;
                }
            },
            openEventDescription(eventID) {
                let self = this;

                this.$router.push('event/' + eventID);
            },
            /*listEvents(isFirst) {
                let self = this;

                let resultListEventsEventsPageFirebase = this.listEventsEventsPageFirebase(isFirst);

                resultListEventsEventsPageFirebase
                    .then(() => {
                        self.isNextEventsPageEmpty();
                    })
                    .catch((error) => {
                        self.buttonIsDisabled = true;

                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },
            isNextEventsPageEmpty() {
                let self = this;

                let resultisNextEventsEventsPageEmptyFirebase = this.isNextEventsEventsPageEmptyFirebase();

                resultisNextEventsEventsPageEmptyFirebase
                    .then((isEmpty) => {
                        self.buttonIsDisabled = isEmpty;
                    })
                    .catch((error) => {
                        self.buttonIsDisabled = true;

                        self.snackbarText = error;
                        self.snackbar = true;
                    });
            },*/
            /*geolocate: function () {
                navigator.geolocation.getCurrentPosition(position => {
                    this.center = {
                        lat: position.coords.latitude,
                        lng: position.coords.longitude
                    };
                });
            },
            getMessagingToken () {
                firebase.messaging().getToken().then(async (token) => {
                    if (token) {
                        const currentMessageToken = window.localStorage.getItem('messagingToken')
                        console.log('token will be updated', currentMessageToken != token)
                        if(currentMessageToken != token){
                            await this.saveToken(token)
                        }
                    } else {
                        console.log('No Instance ID token available. Request permission to generate one.');
                        this.notificationsPermisionRequest();
                    }
                }).catch(function(err) {
                    console.log('An error occurred while retrieving token. ', err);
                });
            },
            notificationsPermisionRequest () {
                firebase.messaging().requestPermission()
                    .then(() => {
                        this.getMessagingToken();
                    })
                    .catch((err) => {
                        console.log('Unable to get permission to notify.', err);
                    });
            },
            listenTokenRefresh() {
                const currentMessageToken = window.localStorage.getItem('messagingToken')
                console.log('currentMessageToken', currentMessageToken);
                if(currentMessageToken){
                    firebase.messaging().onTokenRefresh(function() {
                        firebase.messaging().getToken().then(function(token) {
                            this.saveToken(token);
                        }).catch(function(err) {
                            console.log('Unable to retrieve refreshed token ', err);
                        });
                    });
                }
            },
            saveToken(token) {
                console.log('tokens', token)
                axios.post('https://us-central1-tfg-vue-m3m.cloudfunctions.net/GeneralSubscription',{ token })
                    .then(response => {
                        window.localStorage.setItem('messagingToken',token)
                        console.log(response)
                    }).catch((err) => {
                    console.log(err)
                })
            },*/
        },
        computed: {
            /*...mapState({
                evemapnts: state => state.eventsInfo.events,
            })*/
            ...mapGetters('events', {events: 'getEventsToShow'})
        },
        mounted() {
            let self = this;
            let resultResetEvents = self.resetState();

            resultResetEvents.then(() => {
                self.getEvents().then((moreEventsToShow) => {
                    self.buttonIsDisabled = !moreEventsToShow;
                });
            })
                .catch((error) => {
                    self.snackbarText = error;
                    self.snackbar = true;
                });

            //this.geolocate();
/*
            if (this.events.length == 0) {
                this.listEvents(true);
            }
            else{
                this.isNextEventsPageEmpty();
            }*/

        },

    }
</script>

<style scoped>
    .bottom-centered-buttons {
        left: 50%;
        transform: translate(-50%, 0);
    }
</style>